export default {
  name: 'Carousel',
  data() {
    return {
      slide: 0,
      height: '90vh',
      report: 0,
      fullscreen: false
    }
  },
  props: {
    record: {
      type: Object,
      required: true
    }
  },
  watch: {
    fullscreen: function(val) {
      if (val) {
        this.$q.fullscreen.request()
      } else {
        this.$q.fullscreen.exit()
      }
    },
    '$q.fullscreen.isActive'(val) {
      this.fullscreen = val
    }
  },
  methods: {
    onTransition(size) {
      const scrollbarWidth = 17
      $(this.$refs.carousel.$el)
        .find('img')
        .css('max-width', $(this.$refs.carousel.$el).width() - scrollbarWidth)
    }
  }
}
